//update the post
export async function onRequestPost(context) {
    const {
        request, // same as existing Worker API
        env, // same as existing Worker API
        params, // if filename includes [id] or [[path]]
        waitUntil, // same as ctx.waitUntil in existing Worker API
        next, // used for middleware or to fetch assets
        data, // arbitrary space for passing data between middlewares
    } = context;

    //note this is not working locally so will have to test when its on live
    postData = await request.json();
    console.log(postData);
    const theSQL = ``;
    //run the update
    const info = await context.env.DB.prepare(theSQL).run()

    if (info.success == true) {
        return new Response(JSON.stringify({ status: "ok" }), { status: 200 });
    } else
        return new Response(JSON.stringify({ error: "error updating" }), { status: 400 });
}

//get the post
export async function onRequestGet(context) {
    const {
        request, // same as existing Worker API
        env, // same as existing Worker API
        params, // if filename includes [id] or [[path]]
        waitUntil, // same as ctx.waitUntil in existing Worker API
        next, // used for middleware or to fetch assets
        data, // arbitrary space for passing data between middlewares
    } = context;

    //get the data 
     const { searchParams } = new URL(request.url);
    //get the client id
    const postId = searchParams.get('postId');
    console.log(postId);
    if ((postId == undefined) || (postId == ""))
        return new Response(JSON.stringify({ error: "no Id" }), { status: 400 });


    //prepare the query
    const theSQL = `SELECT body from posts where id = '${postId}'`
    const query = context.env.DB.prepare(theSQL);
    //get the result
    //note : we could make this return first and not all 
    const queryResult = await query.first();
    console.log(queryResult)


}
